import { useState, useRef } from 'react';
import { matchesLabel } from '../../scripts/id-util.js';
import SearchResults from './SearchResults.js';

const FormItem = (props) => {
    const [query, setQuery] = useState("");
    const textRef = useRef("");
	const doQuery = !(props.type === 'notes' || props.type === 'findings');

	const deleteSelf = (e) => {
		props.type === 'contraindicated'
			? e.target.parentNode.parentNode.remove()
		 	: e.target.parentNode.remove();
	}

    const setVal = (e) => {
        textRef.current.value = e.target.id;
		textRef.current.setAttribute("data", e.target.getAttribute("item-id"));
        setQuery("");
    }

	const getChecked = (type) => {
		let isChecked = false;
		props.item[type].forEach(item => {
			if (item.label === props.item.meta.contraReqOrder[props.num]?.label) 
				isChecked = true;
		})
		return isChecked;
	}

	const setData = (e) =>
		e.target.setAttribute("data", e.target.value);

    switch (props.type) {
        case 'contraindicated':
            return (
				<div className="mdb-red-item">
					<label className="mdb-block-item">
					<input
						type="text"
						id={`${props.block}-contraindications-${props.num}`}
						className="mdb-text no-left-margin"
						ref={textRef}
						onChange={(e) => setQuery(e.target.value)}
						data={props.item.meta.contraReqOrder[props.num]?.id ?? null}
						defaultValue={props.item.meta.contraReqOrder[props.num]?.label ?? props.item.meta.contraReqOrder[props.num]}
					/>
					<SearchResults list={matchesLabel(query)} setVal={setVal} />
					<button type="button" onClick={e => deleteSelf(e)} >-</button>					
					</label>

					<div className="mdb-red-type">
						<label className="red-type">
							Required
							<input
								type="radio"
								id={`${props.block}-required-${props.num}`}
								name={`${props.block}-red-type-${props.num}`}
								value="required"
								defaultChecked={getChecked("required")}
								/>
						</label>

						<label name="red-type" className="red-type">
							Contraindicated
							<input
								type="radio"
								id={`${props.block}-contraindicated-${props.num}`}
								name={`${props.block}-red-type-${props.num}`}
								value="contraindicated"
								defaultChecked={getChecked("contraindicated")}
								/>
						</label>
					</div>

					<input
						type="text"
						className="mdb-reasoning"
						id={`${props.block}-${props.type}Why-${props.num}`}
						defaultValue={props.item[`${props.type}Why`][props.num]?.label}
						placeholder="Reasoning"
						onBlur={e => setData(e)}
						/>
				</div>
            );

        default:
            return (
				<label className="mdb-block-item">
					<input
					type="text"
					id={`${props.block}-${props.type}-${props.num}`}
					className="mdb-text no-left-margin"
					ref={doQuery ? textRef : null}
					onChange={doQuery ? (e) => setQuery(e.target.value) : null}
					data={props.item[props.type][props.num]?.id ?? null}
					defaultValue={props.item[props.type][props.num]?.label ?? props.item[props.type][props.num]}
					/>
				<SearchResults list={matchesLabel(query)} setVal={setVal} />
				<button type="button" onClick={e => deleteSelf(e)} >-</button>
				</label>
            );
    }
}

export default FormItem;