import React from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";

import { AuthProvider, StaffRoute } from "@meddbriefer/mdb-auth";

import "./styles/default-styles.scss";
import "./styles/home-styles.scss";
import "./styles/scenario-editor-styles.scss";
import "./styles/class-codes-styles.scss";
import "./styles/crit-actions-styles.scss";

import UnauthedLayout from "./components/UnauthedLayout"

import HomePage from "./components/HomePage";
import ScenarioForm from "./components//ScenarioEditor/ScenarioForm";
import ScenariosList from "./components/ScenarioEditor/ScenariosList";
import ClassCode from './components/ClassCodes/ClassCode';
import ClassCodesList from './components/ClassCodes/ClassCodesList';
import CriticalActions from './components/CriticalActions/CriticalActionsForm';
import CriticalActionsFormList from './components/CriticalActions/CriticalActionsFormList';
import ActionAssociations from './components/CriticalActions/ActionAssociations';
import Header from "./components/Header";


const App = () => {
  return (
	<AuthProvider>
        <Router>
	  		<Header />
			<Switch>
				<Route exact path="/" component={HomePage} />
				<StaffRoute exact path="/scenarios" authFlowLayout={UnauthedLayout} component={ScenariosList} />
				<StaffRoute exact path="/scenario/" authFlowLayout={UnauthedLayout} component={ScenarioForm} />
				<StaffRoute exact path="/scenario/:scenarioId" authFlowLayout={UnauthedLayout} component={ScenarioForm} />
				<StaffRoute exact path="/scenario/:scenarioId/:asTemplate" authFlowLayout={UnauthedLayout} component={ScenarioForm} />
				<StaffRoute exact path="/class-codes-list/" authFlowLayout={UnauthedLayout} component={ClassCodesList} />
				<StaffRoute exact path="/class-codes/" authFlowLayout={UnauthedLayout} component={ClassCode} />
				<StaffRoute exact path="/class-codes/:classCode" authFlowLayout={UnauthedLayout} component={ClassCode} />
				<StaffRoute exact path="/debriefer-entry-list/" authFlowLayout={UnauthedLayout} component={CriticalActionsFormList} />
				<StaffRoute exact path="/debriefer-entry/:mdbFormId" authFlowLayout={UnauthedLayout} component={CriticalActions} />
				<StaffRoute exact path="/debriefer-entry/" authFlowLayout={UnauthedLayout} component={CriticalActions} />
				<StaffRoute exact path="/associations/:formId" authFlowLayout={UnauthedLayout} component={ActionAssociations} />
			</Switch>
        </Router>
	</AuthProvider>
  );
};

export default App;
