import FormItem from './FormItem';
import { useState, useRef, useEffect, useContext } from 'react';
import { matchesChecklist } from '../../scripts/id-util.js';
import SearchResults from './SearchResults.js';
import { MDBFormContext } from './CriticalActionsForm.js';

const FormBlock = (props) => {
    const form = useContext(MDBFormContext);
    const info = form.criticalActions[props.block];
    const deleteSelf = (e) => e.target.parentNode.parentNode.remove();
    
    return (
        <div className="mdb-block-wrapper">
            <div className="layer">
                <button type="button" className="delete-self-block" onClick={e => deleteSelf(e)} >-</button>
                <div id="mdb-info" className="mdb-block" >
                    <p className="mdb-block-heading" >Info:</p>
                    <div className="mdb-info-wrapper">
                        <label>
                            Critical action:
                            <input type="text" 
                                className="mdb-text" 
                                id={`${props.block}-label`} 
                                autoComplete={"off"} 
                                defaultValue={info.label} 
                            />
                        </label>
                        <label>
                            Protocol:
                            <input type="text" 
                                className="mdb-text" 
                                id={`${props.block}-protocol`} 
                                autoComplete={"off"} 
                                defaultValue={info.protocol} 
                            />
                        </label>

                        <BlockInfo info={info} label="Before" type="before" block={props.block} />
                        <BlockInfo info={info} label="After" type="after" block={props.block} />
                        <BlockInfo info={info} label="During" type="during" block={props.block} />
                    </div>
                </div>
            </div>

            <div className="layer">
                <div id="mdb-findings" className="mdb-block" >
                    <p className="mdb-block-heading" >Findings:</p>
                    <BlockContent block={props.block} type="findings" item={info} />
                </div>

                <div id="mdb-notes" className="mdb-block" >
                    <p className="mdb-block-heading" >Notes / Comments:</p>
                    <BlockContent block={props.block} type="notes" item={info} />
                </div>
            </div>

            <div className="layer">
                <div id="mdb-green" className="mdb-block" >
                    <p className="mdb-block-heading" >Standard:</p>
                    <BlockContent block={props.block} type="standard" item={info} />
                </div>

                <div id="mdb-yellow" className="mdb-block" >
                    <p className="mdb-block-heading" >Minimal:</p>
                    <BlockContent block={props.block} type="minimal" item={info} />

                    <input
                        type="text"
                        className="mdb-reasoning"
                        id={`${props.block}-minimalWhy`}
                        autoComplete={"off"}
                        defaultValue={info.minimalWhy}
                        placeholder="Reasoning"
                        />
                </div>

                <div id="mdb-red" className="mdb-block" >
                    <p className="mdb-block-heading" >Contraindications:</p>
                    <BlockContent block={props.block} type="contraindicated" item={info} />
                </div>
            </div>
        </div>
    );
}

const BlockContent = (props) => {
    const [num, setNum] = useState(0);
    const info = props.item;

    const more = () => setNum(num + 1);
    const less = () => num > 0 && setNum(num - 1);

    useEffect(() => {
        if (props.item) {
            props.type === "contraindicated"
                ? setNum(info.contraindicated.length + info.required.length)
                : setNum(info[props.type].length)
        }
    }, [info, props.item, props.type]);

    return (
        <>
        {[...Array(num)].map( (e, i) => (
            <FormItem 
                block={props.block}
                num={i} 
                key={i} 
                type={props.type} 
                item={info} 
                edit={less} 
                />
        ))}
        <button type="button" onClick={more} >Add new</button>
        </>
    );
}

const BlockInfo = (props) => {
    const [query, setQuery] = useState("");
    const textRef = useRef(); 

    const setVal = (e) => {
        textRef.current.value = e.target.id;
		textRef.current.setAttribute("data", e.target.getAttribute("item-id"));
        setQuery("");
    }

    return (
        <label className="mdb-block-item">
            {props.label}:
            <input type="text" 
                className="mdb-text" 
                id={`${props.block}-when-${props.type}`}
                ref={textRef}
                onChange={(e) => setQuery(e.target.value)}
                autoComplete={"off"} 
                defaultValue={props.info.when[props.type]} 
            />
        <SearchResults list={matchesChecklist(query)} setVal={setVal} />
        </label>
    );
}

export default FormBlock;