import { useHistory, useLocation } from 'react-router-dom';
import { useContext } from "react"; 
import { AuthContext } from "@meddbriefer/mdb-auth";

const Header = () => {
  const { currentUser, signOutUser } = useContext(AuthContext);
  const history = useHistory();
  const location = useLocation();

	return (
		<header className="title-bar">
		<h1 onClick={() => history.push('/')}>MedDBriefer</h1>

		<nav>
			{location.pathname !== '/' &&
			<button type="button" className="home-bttn" onClick={() => history.push('/')}>Home</button>}

			{currentUser && 
			<button type="button" className="sign-out-bttn" onClick={() => signOutUser()}>Sign Out</button>}
		</nav>
		</header>
	);
}

export default Header;