import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { getCollection, deleteDocument } from '../../scripts/connect-firebase.js';

const COLLECTION_ID = 'scenario-required-actions';
const MDBFormList = () => {
    const [formsList, setFormsList] = useState(null);

    useEffect(() => {
        getCollection(COLLECTION_ID)
            .then(res => setFormsList(res))
            .catch(err => console.error(err));
    }, []);

    return (
        <div className="db-list">
            <h1>MDB Debriefer Forms</h1>
            <Link to={`/debriefer-entry/`} className="new-bttn db-list-item" id="new-mdb-form" >
                <h1>New Critical Actions</h1>
            </Link>
            {
                formsList ?
                formsList.map((ele, i) => (
                    <MDBFormListItem item={ele} key={i} />
                ))
                : <p>Loading...</p>
            }
        </div>
    );
};

const MDBFormListItem = ({item}) => {
    const deleteItem = async () => {
        if(window.confirm("Are you sure you want to do this? This cannot be undone."))
            await deleteDocument(COLLECTION_ID, item.id);
    };

    return (
        <div className="db-list-item">
            <h1>{item.id}</h1>

            <div className="list-button-wrapper">
                <Link to={`/debriefer-entry/${item.id}`} className="action-bttn" >Edit</Link>
                <button type="button" className="action-bttn delete-bttn" onClick={deleteItem}>Delete</button>
            </div>
        </div>
    );
}

export default MDBFormList;