const getChecklistData = (id) => {
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw(`getChecklistData() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    return { id: id, type: metaData.type, label: metaData.label }
}

const getTabLabel = (id) => {
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getTabLabel() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    return { id: id, label: metaData.tabLabel }
}

const getLabel = (id) => {
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getLabel() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    return metaData.label
}

