const getIntvData = (id) => {
    const metaData = INTV_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getIntvData() id: ${id} not found in INTV_META_DATA`)
    }
    return metaData
}


export const checkForDupIDs = () => {
    let IDs = {}
    CHECKLIST_META_DATA.forEach((rec) => {
        if (!(rec.id in IDs)) {
            IDs[rec.id] = 1
        } else {
            IDs[rec.id]++
        }
    })
    INTV_META_DATA.forEach((rec) => {
        if (!(rec.id in IDs)) {
            IDs[rec.id] = 1
        } else {
            IDs[rec.id]++
        }
    })
    const dupIDs = Array.from(Object.entries(IDs)).filter(([id, count]) => count > 1)
    if (dupIDs.length > 0) {
        console.error("Duplicate IDs found!")
        console.log(JSON.stringify(dupIDs, null, 4))
        process.exit(1)
    }
}

const getNamingConventionViolations = (list, prefix) => {
    return list.filter(rec => !rec.id.startsWith(prefix))
}

const reportViolations = (label, properPrefix, violations) => {
    if (violations.length > 0) {
        console.log(`
The following ${label} don't begin with the prefix "${properPrefix}"
--------------------------------------------------------------------------------`)
        violations.forEach((rec) => console.log(rec.id))
    }
}

const anyHaveValues = (...arrs) => {
    // returns if any of the arrays passed to this have lengths > 0
    // perhaps this should be written as a non-1-liner for clarity
    return arrs.map((arr) => arr.length > 0).filter(val => val === true).length > 0
}

// test META DATA for various fields which aren't constants (refer to IDs, etc)
const HEADINGS    = CHECKLIST_META_DATA.filter(rec => rec.type === HEADING)
const ASSESSMENTS = CHECKLIST_META_DATA.filter(rec => rec.type === ASSESS)
const REQ_ACTIONS = CHECKLIST_META_DATA.filter(rec => rec.type === REQURED)
const CL_PROMPTS  = CHECKLIST_META_DATA.filter(rec => rec.type === CL_PROMPT)

const INTVS       = INTV_META_DATA.filter(rec => rec.type === INTERV)
const PROMPT_SETS = INTV_META_DATA.filter(rec => rec.type === PROMPT_SET)
const PROMPTS     = INTV_META_DATA.filter(rec => rec.type === PROMPT)
const ANSWERS     = INTV_META_DATA.filter(rec => rec.type === ANSWER)


export const enforceNamingConventions = () => {
    const badHeadings = getNamingConventionViolations(HEADINGS, "heading-")
    const badAssessments = getNamingConventionViolations(ASSESSMENTS, "assess-")
    const badRequiredActions = getNamingConventionViolations(REQ_ACTIONS, "required-action-")
    const badCheckListPrompts = getNamingConventionViolations(CL_PROMPTS, "cl-prompt-")
    const badIntvs = getNamingConventionViolations(INTVS, "intv-")
    const badPromptSets = getNamingConventionViolations(PROMPT_SETS, "prompt-set-")
    const badPrompts = getNamingConventionViolations(PROMPTS, "prompt-")
    const badAnswers = getNamingConventionViolations(ANSWERS, "answer-")

    if (anyHaveValues(
            badHeadings,
            badAssessments,
            badRequiredActions,
            badPromptSets,
            badCheckListPrompts,
            badPrompts,
            badIntvs,
            badAnswers)) {
        reportViolations("headings", "heading-", badHeadings)
        reportViolations("assessments", "assess-", badAssessments)
        reportViolations("required actions", "required-action-", badRequiredActions)
        reportViolations("checklist prompts", "cl-prompt-", badCheckListPrompts)
        reportViolations("interventions", "intv-", badIntvs)
        reportViolations("prompt sets", "prompt-set-", badPromptSets)
        reportViolations("prompts", "prompt-", badPrompts)
        reportViolations("answers", "answer-", badAnswers)
        process.exit(1)
    }
}

export const checkPrompts = () => {
    const missingSelectionType = PROMPTS.filter((rec) => !("selectionType" in rec))
    const badSelectionType = PROMPTS.filter((rec) => ![ONE, OOM, NONE].includes(rec.selectionType))
    if (!!missingSelectionType.length || !!badSelectionType.length) {
        console.log("missingSelectionType", missingSelectionType)
        console.log("badSelectionType", badSelectionType)
        process.exit(1)
    }
}

export const checkAssessments = () => {
    const missingTabLabels = ASSESSMENTS.filter((rec) => !("tabLabel" in rec))
    if (!!missingTabLabels.length) {
        console.log("Assessments missing tabLabels:", missingTabLabels)
        process.exit(1)
    }
}

