export const INTV_FORMS = {}


const createInterventionForm = ({ intv, prompts = [] }) => {
    const data = Object.assign({}, getIntvData(intv))
    // do I need to do this (do we really need an intv-form id)???
    const id = data.id.replace("intv-", "intv-form-")
    data.id = id
    data.intervention = intv

    data.type = INTV_FORM
    data.prompts = []
    // pass this intervention down so that it can be associatied with the options so we
    // know the context of what intervention was being performed when that answe was selected
    prompts.forEach(prompt => {
        data.prompts.push(...getPromptSet(Object.assign(prompt, {intervention: intv})))
    })
    INTV_FORMS[intv] = data
}

