const mkPhase = ({ phaseType, id,  items = [], subPhases = [], interventionsMenu = [], uiNav = true, dataEntry = true }) => {
    const headingID = `heading-${id}`
    let obj = getChecklistData(headingID)
    obj.id = id
    obj.type = phaseType
    obj.uiNav = uiNav
    obj.dataEntry = dataEntry
    if (subPhases.length && (items.length || interventionsMenu.length)) {
        throw Error(`a ${phaseType} cannot have both subPhases AND items/interventionsMenu`)
    }
    if (subPhases.length) {
        obj.subPhases = subPhases.map(sp => {
            let cld = getChecklistData(`heading-${sp}`)
            cld.id = sp
            return cld
        })
    } else {
        obj.items = items.map(it => getChecklistData(it))
        obj.interventionsMenu = interventionsMenu
    }
    return obj
}

const createPhase = ({id, items=[], subPhases=[], interventionsMenu=[], uiNav = true, dataEntry = true}) => {
    return mkPhase({ phaseType: PHASE, id, items, subPhases, interventionsMenu, uiNav, dataEntry })
}

const createSubPhase = ({ id, items = [], subPhases = [], interventionsMenu = [], uiNav = true, dataEntry = true }) => {
    return mkPhase({phaseType: SUB_PHASE, id, items, subPhases, interventionsMenu, uiNav, dataEntry })
}

