import React, { useState, useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";
import classnames from 'classnames';

import {
	publishDocument,
	getDocument,
	verifyDocument,
	updateDocument,
} from "../../scripts/connect-firebase.js";
import blankTrauma from '../../data/blank-trauma.json';
import BasicInfo from "./BasicInfo.js";
import Steplist from "./Steplist.js";
import Vitals from "./Vitals";
import SAMPLE from "./Sample.js";

const COLLECTION_ID = 'scenarios';

const ScenarioForm = (props) => {
	const { scenarioId } = props.match.params;
	const { asTemplate } = props.match.params;
	const [scenario, setScenario] = useState();
	const [activeTab, setActiveTab] = useState('basic-info');
	const isActiveTab = (name) => name === activeTab;
	const isEdit = !!scenarioId;
	const formEl = useRef(null);
	let history = useHistory();

	// Load and apply selected scenario
	useEffect(() => {
		isEdit ? getDocument(COLLECTION_ID, scenarioId)
			.then(result => setScenario(result))
			.catch(error => console.log(error))
		: setScenario(blankTrauma);
	}, [scenarioId, isEdit]);

	return (
		<div className="scenario-form-wrapper">
			<nav id="tab-selector">
				<div className={classnames("tab-button", {"active-tab": isActiveTab("basic-info")})} 
					 onClick={() => setActiveTab('basic-info')}>Info</div>

				<div className={classnames("tab-button", {"active-tab": isActiveTab("sample")})} 
					 onClick={() => setActiveTab('sample')}>Sample</div>

				<div className={classnames("tab-button", {"active-tab": isActiveTab("vitals")})}
					 onClick={() => setActiveTab('vitals')}>Vitals</div>

				<div className={classnames("tab-button", {"active-tab": isActiveTab("steplist")})} 
					 onClick={() => setActiveTab('steplist')}>Steplist</div>

				<div className={classnames("tab-button", {"active-tab": isActiveTab("actions")})} 
					 onClick={() => setActiveTab('actions')}>Critical Actions</div>

				<button type="submit" id="submit-next" onClick={() => submitForm()}>Submit</button>
			</nav>

			<form id="scenario-form" className="scenario-form" ref={formEl}>
				{
				scenario ?
				<>
					<BasicInfo 	active={isActiveTab('basic-info')} 	scenario={scenario} edit={isEdit} />
					<SAMPLE 	active={isActiveTab('sample')} 		scenario={scenario} />
					<Vitals 	active={isActiveTab('vitals')} 		scenario={scenario} />
					<Steplist 	active={isActiveTab('steplist')}	scenario={scenario} />
					{/* <Actions 	active={isActiveTab('actions')}	scenario={scenario} /> */}
				</>
				: <p>Loading...</p> // While scenario is loading in
				}
			</form>
		</div>
	);

	async function submitForm() {
		let form = formEl.current;

		if (asTemplate === 'template' && form.name.value === scenario.name)
		{
			alert('You can\'t use the same name as the template!');
		}
		else if ( form.name.value == null || form.name.value.trim() === "" || ( !await verifyDocument(COLLECTION_ID, form.name.value) && !isEdit ) ) 
		{
			// If scenario exists and not in edit mode
			alert('Scenario needs a new name!');
		}
		else if (window.confirm("Any empty items will be filled with default information. Submit?"))
		{
			form = processForm(form);
			// console.log(form);

			(isEdit && !asTemplate)
				? await updateDocument(COLLECTION_ID, form.id, form)
				: await publishDocument(COLLECTION_ID, form); // Create new

			history.push("/scenarios"); // Send back once submitted
		} 
		else 
		{
			console.log("Submit cancelled.");
		}
	}

	function processForm(data) {
		let input, id, path, 
		newScenario = deepCopy(scenario);
		
		for(let item of data) {
			id = item.id;
			path = id.split('-');
			input = item.value;

			// Default assess case
			if (path[0] === 'assessmentFindings' && !input) 
				input = "Unremarkable";

			if (input) { // Don't process empty fields
				// Reconstruct assessmentFindings path after split
				if (path[0] === 'assessmentFindings') 
					path = [path[0], id.split('assessmentFindings-')[1]];

				// Read numbers as numbers
				input = isNaN(input) ? input : parseInt(input);

				// Handle different object depths
				switch (path.length) {
					case 1: newScenario[path[0]] = input; break;
					case 2: newScenario[path[0]][path[1]] = input; break;
					case 3: newScenario[path[0]][path[1]][path[2]] = input; break;

					default: console.error('Path length error:', path.length); break;
				}
			}
		}

		newScenario.id = newScenario.name;

		return newScenario;
	}
};

function deepCopy(obj) {
	return JSON.parse(JSON.stringify(obj));
}

export default ScenarioForm;
